.vaccine-app-plan {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  color: #333;
}

.vaccine-app-plan .picker-list-item {
  height: 0.75rem;
  line-height: 0.75rem;
}

.vaccine-app-plan .picker-list-item .am-list-extra {
  font-size: 0.24rem;
  text-align: left;
  position: absolute;
}

.vaccine-app-plan .picker-list-item .am-list-arrow {
  margin-left: 4rem;
}

.vaccine-app-plan .am-picker .am-picker-col {
  height: 6rem;
}

.vaccine-app-plan .am-picker .am-picker-col .am-picker-col-mask {
  height: 100%;
}

.vaccine-app-plan .am-picker .am-picker-col .am-picker-col-indicator {
  height: 0.4rem!important;
  line-height: 0.4rem!important;
  top: 50%!important;
  margin-top: -0.2rem;
}

.vaccine-app-plan .am-picker .am-picker-col .am-picker-col-content .am-picker-col-item {
  height: 0.4rem!important;
  line-height: 0.4rem!important;
  font-size: 0.3rem!important;
}

.vaccine-app-plan .am-calendar .time-picker .title {
  height: 0.8rem;
  font-size: 0.36rem;
}

.vaccine-app-plan .am-calendar .confirm-panel .button {
  height: 0.4rem;
  line-height: 0.4rem;
  font-size: 0.36rem;
}

.vaccine-app-plan .am-calendar .content .header .title {
  font-size: 0.34rem;
  height: 0.4rem;
}

.vaccine-app-plan .am-calendar .content .header .left {
  top: 0.15rem;
}

.vaccine-app-plan .am-calendar .content .header .left .am-icon-md {
  width: 0.4rem;
  height: 0.4rem;
}

.vaccine-app-plan .am-calendar .content .header .right {
  top: 0.15rem;
  font-size: 0.3rem;
}

.vaccine-app-plan .am-calendar .content .am-calendar.date-picker .week-panel .cell {
  font-size: 0.36rem;
  height: 0.4rem;
  line-height: 0.4rem;
}

.vaccine-app-plan .am-calendar .content .am-calendar.date-picker .wrapper .months .date .cell .date {
  font-size: 0.3rem;
}

.vaccine-app-plan .row-wrapper::after {
  clear: both;
  content: '';
  display: block;
  width: 0;
  height: 0;
  visibility: hidden;
}

.vaccine-app-plan .row-wrapper {
  padding: 0.15rem 0.3rem;
  font-size: 0.3rem;
}

.vaccine-app-plan .row-wrapper.last {
  margin: 0.3rem auto;
}

.vaccine-app-plan .row-wrapper.first {
  padding: 0;
  height: 0.6rem;
  line-height: 0.6rem;
  background-color: #ccf0e2;
  color: #ff7043;
  text-align: center;
}

.vaccine-app-plan .row-wrapper.second {
  margin-top: 0.15rem;
}

.vaccine-app-plan .row-wrapper .input-wrapper {
  margin-top: 0.15rem;
  width: 100%;
  position: relative;
}

.vaccine-app-plan .row-wrapper .input-wrapper .time-input {
  text-indent: 0.15rem;
  width: 100%;
  height: 0.75rem;
  line-height: 0.75rem;
  border: 0.01rem solid #e5e5e5;
  border-radius: 0.04rem;
  font-size: 0.28rem;
}

.vaccine-app-plan .row-wrapper .input-wrapper .icon_calendar {
  width: 0.6rem;
  height: 0.6rem;
  background-size: 80% 80%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  position: absolute;
  top: 0.15rem;
  right: 0.15rem;
}

.vaccine-app-plan .row-wrapper .vaccine-wrapper {
  margin-top: 0.15rem;
  float: left;
  box-sizing: border-box;
  width: 100%;
}

.vaccine-app-plan .row-wrapper .vaccine-wrapper .am-list-content {
  font-size: 0.28rem;
}

.vaccine-app-plan .row-wrapper .vaccine-wrapper .am-list-item {
  width: 47%;
  float: left;
  line-height: 0.3rem;
  padding-left: 0.2rem;
}

.vaccine-app-plan .row-wrapper .vaccine-wrapper .am-list-item .am-list-line::after {
  background: none;
}

.vaccine-app-plan .row-wrapper .vaccine-wrapper .am-list-item .am-checkbox-inner {
  border-radius: 0%!important;
}

.vaccine-app-plan .row-wrapper .vaccine-wrapper .am-list-item .am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #06cc7e;
  background: #06cc7e;
}

.vaccine-app-plan .row-wrapper .addSMS-radio {
  float: left;
}

.vaccine-app-plan .row-wrapper .addSMS-radio .am-checkbox {
  width: 0.3rem;
  height: 0.3rem;
  margin-right: 0.15rem;
}

.vaccine-app-plan .row-wrapper .phone-input {
  display: block;
  float: left;
  margin-top: 0.15rem;
  width: 98%;
  height: 0.75rem;
  line-height: 0.75rem;
  border-radius: 0.04rem;
}

.vaccine-app-plan .row-wrapper .phone-input .am-input-control input {
  font-size: 0.28rem;
}

.vaccine-app-plan .row-wrapper .sms-wrapper {
  width: 100%;
  float: left;
  margin-top: 0.15rem;
  margin-bottom: 0.15rem;
}

.vaccine-app-plan .row-wrapper .sms-wrapper .am-list-line::after,
.vaccine-app-plan .row-wrapper .sms-wrapper .am-list-item:not(:last-child) {
  background-color: unset!important;
}

.vaccine-app-plan .row-wrapper .sms-wrapper .am-list-item {
  width: 30%;
  float: left;
  line-height: 0.3rem;
  background-color: unset!important;
}

.vaccine-app-plan .row-wrapper .sms-wrapper .am-list-item .am-list-content {
  font-size: 0.24rem;
}

.vaccine-app-plan .row-wrapper .sms-content textarea {
  height: 1rem;
  font-size: 0.28rem;
  line-height: 0.4rem;
}

.vaccine-app-plan .row-wrapper .submit-btn {
  margin: 0 auto;
  width: 2rem;
  height: 0.7rem;
  line-height: 0.7rem;
  border-radius: 0.1rem;
  font-size: 0.34rem;
  background-color: #06cc7e;
  color: #FFF;
}

.vaccine-app-plan .row-wrapper .am-checkbox {
  width: 0.3rem;
  height: 0.3rem;
}

.vaccine-app-plan .row-wrapper .am-checkbox .am-checkbox-inner {
  width: 0.3rem;
  height: 0.3rem;
}

.vaccine-app-plan .row-wrapper .am-checkbox .am-checkbox-inner:after {
  width: 0.09rem;
  height: 0.16rem;
  right: 0.09rem;
  border-width: 0 0.03rem 0.03rem 0 !important;
}

.vaccine-app-plan .row-wrapper .am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #06cc7e;
  background: #06cc7e;
}